import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import { deepClone } from '@/until';
import textButton from '@/components/common-components/text-btn.vue';
let AddForm = class AddForm extends Vue {
    constructor() {
        super(...arguments);
        // ----------------内部属性-----------------------
        this.validity = {};
        this.valid = undefined;
        this.model = {
            name: '',
            price: 0,
            num: 0,
            img: '',
            total: 0,
            goodsType: 'delivery' // 商品类型 1.delivery 配送型 2.arrival 到店核销
        };
        this.imageList = [];
        this.isEdit = false;
        this.options = {
            scrollToInvalidField: true,
            layout: 'classic' // classic fresh
        };
        this.checkList = [];
        this.groupOptions = [];
        // ----------------生命周期-----------------------
    }
    // ----------------计算属性-----------------------
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    mounted() {
        let tagList = deepClone(this.schoolInfo.tagList) || [];
        this.groupOptions = tagList.map((item) => {
            return {
                label: item.name,
                value: item.id
            };
        });
        this.checkList = deepClone(this.currentTags);
    }
    // ----------------监听方法-----------------------
    // ----------------调用方法-----------------------
    resetHandler() {
        this.$emit('cancel');
    }
    submitHandler(e) {
        e.preventDefault();
        this.$emit('change', this.checkList);
    }
    setData(model) {
        if (model.img) {
            this.$set(this, 'imageList', [model.img]);
        }
        const initMode = {
            ...deepClone(this.model),
            ...model
        };
        this.$set(this, 'model', initMode);
    }
    addSpec() {
        let menu = (this.model.menu && this.model.menu.length > 0) ? deepClone(this.model.menu) : [];
        let index = (menu && menu.length) ? menu.length : 0;
        let specObj = {
            id: this.model.id + '' + index,
            num: 0,
            price: 0,
            spec: ''
        };
        menu.push(specObj);
        this.$set(this.model, 'menu', menu);
    }
    deleteSpec(index) {
        console.log(index, '');
        let menu = deepClone(this.model.menu);
        menu.splice(index, 1);
        this.$set(this.model, 'menu', menu);
    }
    submitClick() {
        this.$refs.editForm.submit(false);
    }
    resetClick() {
        this.$refs.editForm.reset();
    }
