import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import editForm from './edit-food.vue';
import addNav from './add-nav.vue';
import { deepClone } from '@/until';
import textButton from '@/components/common-components/text-btn.vue';
import imageUpload from '@/components/common-components/image-upload.vue';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.showEditbox = false;
        this.showAddNav = false;
        this.pageTitle = '店铺信息';
        this.data = [];
        this.currentIndex = 0;
        this.modelIndex = 0;
        this.headImgList = [];
        this.model = {
            title: '',
            markLight: '',
            dtConfig: ['delivery', 'selflifting']
        };
        this.imageList = [];
        this.isEdit = false;
        this.currentTags = [];
        this.fields = [
            {
                type: 'input',
                modelKey: 'title',
                label: '店铺名称',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                trigger: 'blur',
                messages: { required: '店铺名称必填' },
            },
            {
                type: 'input',
                modelKey: 'markLight',
                label: '所属餐厅',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: false
                },
                trigger: 'blur',
                messages: { required: '店铺名称必填' },
            },
            {
                type: 'checkbox-group',
                label: '运营方式',
                modelKey: 'dtConfig',
                props: {
                    options: [
                        {
                            label: '帮手配送',
                            value: 'delivery'
                        },
                        {
                            label: '用户自取',
                            value: 'selflifting'
                        }
                    ]
                },
                rules: {
                    required: true
                },
                messages: {
                    required: '必须选择一种运营方式'
                }
            },
            {
                type: 'submit',
                label: '保存'
            }
        ];
        this.options = {
            scrollToInvalidField: true,
            layout: 'classic' // classic fresh
        };
    }
    get shopId() {
        return this.$route.params.id;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    created() {
        let shopData = deepClone(this.$route.params);
        this.model = {
            title: shopData.title,
            markLight: shopData.markLight,
            dtConfig: shopData.dtConfig || ['delivery', 'selflifting'],
            tags: shopData.tags || []
        };
        let currentTags = shopData.tags || [];
        this.getTags(currentTags);
        this.headImgList = [shopData.headImg];
    }
    changeSuccess(checkList) {
        this.model.tags = checkList;
        this.showEditbox = false;
    }
    getTags(tags) {
        let tagList = this.schoolInfo.tagList || [];
        let returnTags = [];
        tagList.forEach((item) => {
            if (tags.indexOf(item.id) > -1) {
                returnTags.push(item.id);
            }
        });
        this.currentTags = returnTags;
    }
    addTag() {
        this.showEditbox = true;
    }
    // ----------------计算属性-----------------------
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    // ----------------监听方法-----------------------
    // ----------------调用方法-----------------------
    resetHandler() {
        this.$emit('cancel');
    }
    submitHandler(e) {
        e.preventDefault();
        console.log(this.headImgList, 'iiiiiiiiiiiiii');
        let setData = { ...this.model, headImg: this.headImgList[0] || '"http://mmbiz.qpic.cn/mmbiz_jpg/5XHic5LQPCDh6os0mLo4IgYAfsHUicgR1ic3RsGfo19PkzF5ictOueZ9ktuOyuC4XbwAADNbrMibjuicy1JYwojhG36w/0"' };
        this.$store.dispatch('updateShopInfoByObj', {
            shopId: this.shopId,
            value: setData
        }).then((e) => {
            if (e.success) {
                this.$createDialog({
                    type: 'alert',
                    content: '保存成功',
                    onConfirm: (e) => {
                        this.$router.push({
                            name: 'businessManagement',
                            params: {
                                id: this.shopId,
                                oprate: 'update',
                                data: setData
                            }
                        });
                    }
                }).show();
            }
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox,
            editForm,
            addNav,
            textButton,
            imageUpload
        }
    })
], businessManagement);
export default businessManagement;
